import { Component } from '@angular/core';
import {RestoreRestService } from 'src/app/restore-rest.service';
import { BikeComponent } from './shared/model/restore-parts/bike-component';
import { Part } from './shared/model/restore-parts/part';
import { ComponentType } from 'src/app/shared/model/restore-parts/component-type';
import { PartColour } from './shared/model/restore-parts/part-colour';
import { PartDescription } from './shared/model/restore-parts/part-description';
import { RestorePart } from './shared/model/restore-parts/restore-part';
import { RestoreData } from './data/restore-data';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  readonly restoreData : RestoreData;  

  constructor(public restoreApi:RestoreRestService){    
    this.restoreData = new RestoreData(this.restoreApi);
  }

  
  title = 'St Neots Motorcyles GP Restorations';
  products:string ='nothing';
  componentTypes: ComponentType[];
  bikeComponents : BikeComponent[];
  parts: Part[];
  colours : PartColour[];
  descriptions : PartDescription[];

  saveRestorePart(part:RestorePart){
    this.restoreData.saveRestorePart(part);
  }  

  ngOnInit() {
    this.restoreData.getProducts();
    this.bikeComponents = this.restoreData.getBikeComponents();
    this.parts = this.restoreData.getParts();
    this.componentTypes = this.restoreData.getComponentTypes();
    this.colours = this.restoreData.getColours();
    this.descriptions = this.restoreData.getDescriptions();
  }  
}
