import { Component, OnInit, Input, ViewChild, EventEmitter, Output } from '@angular/core';
import { FormControl, NgForm } from '@angular/forms';
import { Observable, from } from 'rxjs';
import { map, startWith, distinct, } from 'rxjs/operators';
import { BikeComponent } from '../../shared/model/restore-parts/bike-component';
import { ComponentType } from '../../shared/model/restore-parts/component-type';
import { Part } from '../../shared/model/restore-parts/part';
import { PartColour } from '../../shared/model/restore-parts/part-colour';
import { RestorePart } from '../../shared/model/restore-parts/restore-part';
import { PartDescription } from '../../shared/model/restore-parts/part-description';
import { NamedModel } from 'src/app/shared/model/restore-parts/restore-parts-interfaces';

@Component({
  selector: 'app-part-add',
  templateUrl: './part-add.component.html',
  styleUrls: ['./part-add.component.css']
})
export class PartAddComponent implements OnInit {
  @Input() bikeComponents: BikeComponent[];
  @Input() componentTypes: ComponentType[];
  @Input() parts: Part[];
  @Input() partColours: PartColour[];
  @Input() partDescriptions: PartDescription[];

  @Output() addRestorePart = new EventEmitter<RestorePart>();

  @ViewChild('componentField') fieldComponent;
  @ViewChild('typeField') fieldType;
  @ViewChild('partNameField') fieldPart;
  @ViewChild('partDescField') fieldDesc;
  @ViewChild('colourField') fieldColour;

  components: string[];
  types: string[];
  partNames: string[];
  descriptions: string[];
  colours: string[];
  disableType: boolean;


  restorePart: RestorePart;
  status: string;

  ngOnInit() {
    this.restorePart = new RestorePart();
    this.restorePart.quantity = 1;
    this.restorePart.sent = true;
    this.components = this.bikeComponents.map(c => c.name);
    this.types = this.componentTypes.map(c => c.name);
    this.partNames = this.parts.map(p => p.name);
    this.descriptions = this.partDescriptions.map(p => p.name);
    this.colours = this.partColours.map(p => p.name);
    this.disableType = false;
    this.status = '';

  }

  onSubmit() {
      if (!this._partValid()) {
      this.status = "Missing information";
    }
    else {
      this.status = "";
      this._populateRestorePart();
      this.addRestorePart.emit(this.restorePart);
    }
  }

  compnentBlur() {
    console.log("on component blur");
    var existingComponent = this._getSelectedItemByName(this.fieldComponent, this.bikeComponents);
    if (existingComponent) {
      console.log("disable type");
      this.disableType = true;
      this._fieldSetValue(this.fieldType, this._getTypeForSelectedComponent().name);
    } else {
      console.log("enable type");
      this.disableType = false;
      this.fieldType.form.setValue('');
    }
  }

  _populateRestorePart() {
    this._populateRestoreComponentId();
    this._populateRestoreTypeId();
    this._populateRestorePartId();
    this._populateRestorePartDescriptionId();
  }

   _getTypeForSelectedComponent(): ComponentType {
    var existingComponent = this._getSelectedItemByName(this.fieldComponent, this.bikeComponents);
    if (existingComponent) {
      var compType = this.componentTypes.find(t => t.id == existingComponent.typeId);
      return compType;
    }
  }

  _populateRestoreComponentId() {
    var existingComponent = this._getSelectedItemByName(this.fieldComponent, this.bikeComponents);
    if (existingComponent) {
      this.restorePart.componentId = existingComponent.id;
    } else {
      // POST Component
      console.log('NOTexistingComp:');
      this.restorePart.componentId = null;
    }
  }

  _populateRestoreTypeId() {
    var existingType = this._getSelectedItemByName(this.fieldType, this.componentTypes);
    if (existingType) {
      this.restorePart.typeId = existingType.id;
    }
  }

  _populateRestorePartId() {
    var existingPart = this._getSelectedItemByName(this.fieldPart, this.parts);
    if (existingPart) {
      this.restorePart.partId = existingPart.id;
    } else {
      // POST PART
      console.log('NOTexistingPart');
      this.restorePart.partId = null;
    }
  }

  _populateRestorePartDescriptionId() {
    var existingDesc = this._getSelectedItemByName(this.fieldDesc, this.partDescriptions);
    if (existingDesc) {
      this.restorePart.descriptionId = existingDesc.id;
    } else {
      // POST DESC
      console.log('NOTexistingDesc');
      this.restorePart.descriptionId = null;
    }
  }

  _populateRestoreColourId() {
    var existingColour = this._getSelectedItemByName(this.fieldColour, this.partColours);
    if (existingColour) {
      this.restorePart.colourId = existingColour.id;
    } else {
      // POST COLOUR
      console.log('NOTexistingColour:');
      this.restorePart.colourId = null;
    }
  }

  
  private _partValid(): boolean {
    console.log("Checking Form Valid...");
    return this._fieldValid(this.fieldColour); // && ...&&
  }

  private _fieldValue(field: any) {
    return field.form.value;
  }

  private _fieldValid(field: any) {
    return field.form.valid;
  }

  private _fieldSetValue(field: any, value:any){
    field.form.setValue(value);
  }

  private _getSelectedItemByName<T extends NamedModel>(field: any, collection: T[]): T {
    var selectedItem = this._fieldValue(field);
    if (selectedItem) {
      var existingItem = collection.find(c => c.name.toLowerCase() == selectedItem.toLowerCase());
      return existingItem;
    } else {
      return undefined;
    }
  }
}



